.videoFooter {
  position: relative;
  bottom: 130px;
  color: rgb(255, 255, 255);
  margin-left: 20px;
}

.videoFooter__text h3 {
  font-size: medium;
  padding-bottom: 15px;
}

.videoFooter__text p {
  padding-bottom: 10px;
  font-size: small;
}

.videoFooter__music {
  display: flex;
}

.videoFooterMusic__text {
  width: 80%;
  overflow-x: hidden;
}
.videoFooterMusic__text p {
  animation: moveTheText 5s infinite linear;
}

.videoFooter__record {
  animation: spinTheRecord 3s infinite linear;
  position: absolute;
  bottom: 3px;
  right: 23px;
  height: 30px;
  filter: invert(1);
}

@keyframes spinTheRecord {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes moveTheText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@media only screen and (max-width: 440px) {
  .videoFooter {
    bottom: 20%;
  }
  .videoFooterMusic__text {
    width: 78%;
  }
}
